import { createSlice } from '@reduxjs/toolkit';
import { Phone } from '../types/phone';

type BasketState = {
  basketPhone: Phone[]
};

const initialState: BasketState = {
  basketPhone: [],
};

const basketSlice = createSlice({
  name: 'basket',
  initialState,
  reducers: {
    addPhone(state, action) {
      const newPhone = action.payload;

      // eslint-disable-next-line no-param-reassign
      state.basketPhone = state.basketPhone.filter(p => p.id !== newPhone.id);

      state.basketPhone.push(newPhone);
    },
    addPrice(state, action) {
      const { phoneId } = action.payload;
      const phone = state.basketPhone.find(ph => ph.phoneId === phoneId);

      if (phone) {
        phone.price += phone.price;
      }
    },
    minusPrice(state, action) {
      const { phoneId } = action.payload;
      const phone = state.basketPhone.find(ph => ph.phoneId === phoneId);

      if (phone) {
        phone.price -= phone.price;
      }
    },
  },
});

export const { addPrice, minusPrice, addPhone } = basketSlice.actions;
export default basketSlice.reducer;
