import { useEffect, useState } from 'react';
import { Link, useParams } from 'react-router-dom';
import { MobileHome } from '../MobileHome/MobileHome';
import './style.scss';
import { useHeaderContext } from '../../provider/HeaderContext';
import { HotPrice } from '../HotPrice/HotPrice';

type Description = {
  title: string;
  text: string[];
};

export const PhoneDetails = () => {
  const { phoneId } = useParams();
  const currentUrl = window.location.hash.replace('#/', '');
  const linkColor = currentUrl.split('-').slice(0, -1).join('-');
  const linkCapacity = currentUrl.split('-').slice(0, -2).join('-');
  const lastItem = currentUrl.split('-').slice(-1).join('');

  const {
    favoritePhones,
    basketPhones,
    addToBasketWithName,
    addToFavoritesWithName,
    mobiles,
  } = useHeaderContext();
  const [phones, setPhones] = useState<{
    name: string;
    images: string[];
    colorsAvailable: string[]
    capacityAvailable: string[]
    priceRegular: number
    priceDiscount: number
    screen: string
    resolution: string
    processor: string
    ram : string
    camera: string
    zoom: string
    cell: string[]
    id: string
    description: Description[]
  } | null>(null);

  const mayAlsoLikes = mobiles.filter(phone => phone.year === 2019);

  const [selectImg, setSelectImg] = useState<string>('');

  useEffect(() => {
    fetch(`api/products/${phoneId}.json`)
      .then(response => response.json())
      .then(data => setPhones(data));
  }, [phoneId]);

  if (!phones) {
    return null;
  }

  const {
    name,
    images,
    colorsAvailable,
    capacityAvailable,
    priceRegular,
    priceDiscount,
    id,
    screen,
    resolution,
    processor,
    ram,
    camera,
    zoom,
    cell,
    description,
  } = phones;

  return (
    <div className="details">
      <MobileHome />
      <div className="details__content">
        <h1
          className="details__content-title"
        >
          {name}
        </h1>
        <div className="details__content-phone">
          <div className="details__content-phone-left">
            {images.map(image => (
              <div
                className="images"
                role="button"
                key={image}
                tabIndex={0}
                onClick={() => setSelectImg(image)}
                onKeyDown={() => setSelectImg(image)}
              >
                <img
                  className="images__left"
                  src={image}
                  alt="img"
                />
              </div>
            ))}
          </div>
          <div className="details__content-phone-main">
            <img className="phone-img" src={selectImg || images[0]} alt="" />
          </div>
          <div className="details__content-phone-info">
            <p
              className="details__content-phone-info-title"
            >
              Available colors
            </p>
            <div className="details__content-phone-info-circles">
              {colorsAvailable.map(color => (
                <Link to={`/${linkColor}-${color}`} key={color}>
                  <div
                    className="details__content-phone-info-circle"
                    style={{ background: color }}
                  />
                </Link>
              ))}
            </div>
            <div className="line" />
            <p
              className="details__content-phone-info-title"
            >
              Select capacity
            </p>
            <div
              className="details__content-phone-info-capacity"
            >
              {capacityAvailable.map(memory => (
                <Link
                  className={`memory ${currentUrl.includes(memory.toLowerCase()) ? 'is-active' : ''}`}
                  key={memory}
                  to={`/${linkCapacity}-${memory.toLowerCase()}-${lastItem}`}
                >
                  {memory}
                </Link>
              ))}
            </div>

            <div className="line" />

            <div className="price">
              <p className="price__discount">{`$${priceDiscount}`}</p>
              <p className="price__regular">{`$${priceRegular}`}</p>
            </div>
            <div className="details__buttons">
              <button
                id={id}
                type="button"
                className={basketPhones.find(p => p.phoneId === id)
                  ? 'details__buttons-add is-active' : 'details__buttons-add'}
                onClick={() => addToBasketWithName(name)}
              >
                {basketPhones.find(p => p.phoneId === id)
                  ? 'Added to cart' : 'Add to cart'}
              </button>
              <button
                type="button"
                className="details__buttons-heart"
                onClick={() => addToFavoritesWithName(name)}
              >
                {favoritePhones.find(p => p.phoneId === id) ? (
                  <img
                    className="details__buttons-heart-image"
                    src="./img/icons/HeartLike.svg"
                    alt="heart"
                  />
                ) : (
                  <img
                    className="details__buttons-heart-image"
                    src="./img/icons/Heart.svg"
                    alt="heart"
                  />
                )}
              </button>
            </div>
            <div>
              <div className="details__screen">
                <p className="details__screen-names">Screen</p>
                <p className="details__screen-info">{screen}</p>
              </div>
              <div className="details__resolution">
                <p className="details__resolution-names">Resolution</p>
                <p className="details__resolution-info">{resolution}</p>
              </div>
              <div className="details__processor">
                <p className="details__processor-names">Processor</p>
                <p className="details__processor-info">{processor}</p>
              </div>
              <div className="details__ram">
                <p className="details__ram-names">Ram</p>
                <p className="details__ram-info">{ram}</p>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className="information">
        <div className="information__about">
          <h1 className="information__title">About</h1>
          <div className="information__line" />
          {description.map(item => (
            <div key={item.title} className="information__section">
              <h2 className="information__section-title">{item.title}</h2>
              <div className="information__section-text">
                <p>{item.text}</p>
              </div>
            </div>
          ))}
        </div>

        <div className="information__tech">
          <h1 className="information__title">Tech specs</h1>
          <div className="information__line" />
          <div>
            <div className="information__type">
              <p className="information__type-names">Screen</p>
              <p className="information__type-info">{screen}</p>
            </div>
            <div className="information__type">
              <p className="information__type-names">Resolution</p>
              <p className="information__type-info">{resolution}</p>
            </div>
            <div className="information__type">
              <p className="information__type-names">Processor</p>
              <p className="information__type-info">{processor}</p>
            </div>
            <div className="information__type">
              <p className="information__type-names">Ram</p>
              <p className="information__type-info">{ram}</p>
            </div>
            <div className="information__type">
              <p className="information__type-names">Camera</p>
              <p className="information__type-info">{camera}</p>
            </div>
            <div className="information__type">
              <p className="information__type-names">Zoom</p>
              <p className="information__type-info">{zoom}</p>
            </div>
            <div className="information__type">
              <p className="information__type-names">Cell</p>
              <p className="information__type-info">{cell.join(', ')}</p>
            </div>
          </div>
        </div>
      </div>
      <HotPrice
        phones={mayAlsoLikes}
        title="You may also like"
        showOldPrice={!false}
      />
    </div>
  );
};

export default PhoneDetails;
