import React, {
  createContext,
  useContext,
  useState,
  ReactNode,
  useEffect,
} from 'react';
import { Phone } from '../types/phone';

interface HeaderContextType {
  inputValue: string;
  handleSearch: (event: React.ChangeEvent<HTMLInputElement>) => void;
  favoritePhones: Phone[];
  addToFavorite: (favoritePhones: Phone) => void;
  basketPhones: Phone[];
  addToBasket: (basketPhones: Phone) => void;
  handlerDelete: (basketPhone: Phone) => void;
  addToBasketWithName: (name: string) => void;
  addToFavoritesWithName: (name: string) => void;
  mobiles: Phone[]
}

const HeaderContext = createContext<HeaderContextType | undefined>(undefined);

export const useHeaderContext = () => {
  const context = useContext(HeaderContext);

  if (!context) {
    throw new Error('useHeaderContext must be used within a HeaderProvider');
  }

  return context;
};

interface HeaderProviderProps {
  children: ReactNode;
}

export const HeaderProvider: React.FC<HeaderProviderProps> = ({ children }) => {
  const [inputValue, setInputValue] = useState<string>('');
  const [favoritePhones, setFavoritePhones] = useState<Phone[]>([]);
  const [basketPhones, setBasketPhones] = useState<Phone[]>([]);
  const [mobiles, setMobiles] = useState<Phone[]>([]);

  useEffect(() => {
    fetch('api/products.json')
      .then(response => response.json())
      .then(data => setMobiles(data));
  }, [setMobiles]);

  const handleSearch = (event: React.ChangeEvent<HTMLInputElement>) => {
    setInputValue(event.target.value);
  };

  const addToFavorite = (phone: Phone) => {
    if (favoritePhones.some(favoritePhone => favoritePhone.id === phone.id)) {
      setFavoritePhones(prev => prev.filter(
        favoritePhone => favoritePhone.id !== phone.id,
      ));
    } else {
      setFavoritePhones(prev => [...prev, phone]);
    }
  };

  const addToBasket = (phone: Phone) => {
    if (basketPhones.some(basketPhone => basketPhone.id === phone.id)) {
      setBasketPhones(prev => prev
        .filter(basketPhone => basketPhone.id !== phone.id));
    } else {
      setBasketPhones(prev => [...prev, phone]);
    }
  };

  const addToBasketWithName = (name: string) => {
    const phoneToAdd = mobiles.find(phone => phone.name === name);

    if (phoneToAdd) {
      if (basketPhones.some(basketPhone => basketPhone.phoneId === name)) {
        setBasketPhones(prev => prev
          .filter(basketPhone => basketPhone.phoneId !== name));
      } else {
        setBasketPhones(prev => [...prev, phoneToAdd]);
      }
    }
  };

  const addToFavoritesWithName = (name: string) => {
    const phoneToAdd = mobiles.find(phone => phone.name === name);

    if (phoneToAdd) {
      if (favoritePhones.some(favorite => favorite.phoneId === name)) {
        setFavoritePhones(prev => prev
          .filter(favorite => favorite.phoneId !== name));
      } else {
        setFavoritePhones(prev => [...prev, phoneToAdd]);
      }
    }
  };

  const handlerDelete = (phone: Phone) => {
    setBasketPhones(prevBasketPhones => prevBasketPhones
      .filter(basketPhone => basketPhone.id !== phone.id));
  };

  return (
    <HeaderContext.Provider value={{
      inputValue,
      handleSearch,
      favoritePhones,
      addToFavorite,
      basketPhones,
      addToBasket,
      handlerDelete,
      addToBasketWithName,
      addToFavoritesWithName,
      mobiles,
    }}
    >
      {children}
    </HeaderContext.Provider>
  );
};
